import React, { useState, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CInput,
  CLabel,
  CFormGroup,
  CDataTable,
  CAlert,
  CRow,
  CSelect,
  CButton,
  CPagination
} from '@coreui/react'
import config from '../../config'
import axios from 'axios'
import { Modal } from 'antd'
import { ExclamationCircleOutlined } from '@ant-design/icons'

const Users = () => {
  const history = useHistory()
  const queryPage = useLocation().search.match(/page=([0-9]+)/, '')
  const currentPage = Number(queryPage && queryPage[1] ? queryPage[1] : 1)
  const [page, setPage] = useState(currentPage)
  const [setlementsDatas, setSettlementsDatas] = useState([])
  const [createSettlement, setCreate] = useState(false)

  const [currency, setCurrency] = useState('')
  const [rate, setRate] = useState('')
  const [cap, setCap] = useState('')
  const [lboundary, setLBoundary] = useState('')
  const [uboundary, setUBoundary] = useState('')
  const [base, setBase] = useState('')


  const [updateState, setUpdateState] = useState('')
  const [id, setID] = useState('')

  const [msg, setMsg] = useState(false)
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState(false)
  const [fill, setFill] = useState(false)
  const [load, setLoad] = useState(false)

  const [deleteErr, setDelErr] = useState(false)
  const [delSuccess, setDelSuccess] = useState(false)

  const closeCreate = () => {
    setCreate(false)
  }

  const pageChange = newPage => {
    currentPage !== newPage &&
    axios
    .get(
      `${config.baseUrl}/api/v1/admin/transactions/transfer?pageNumber=${newPage}&pageSize=10`,
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      if (res.status === 200) {
        setSettlementsDatas(
          res.data.records.map(data => ({
            reference: data.reference,
            firstName: data.traderXUser.firstName,
            lastName: data.traderXUser.lastName,
            emailAddress: data.traderXUser.emailAddress,
            amount: Intl.NumberFormat('en-US').format(data.amount),
            provider: data.provider.replace('_', ' '),
            transactionStatus: data.transactionStatus,

            country: data.traderXUser.country.replace('_', ' ')
          }))
          )
      }
    })
    .catch(err => {
      if (err) {
      }
    })
  }

  const addSettlement = () => {
    if (currency && lboundary && uboundary && rate && cap) {
      setLoad(true)
      axios
      .post(
        `${config.baseUrl}/api/v1/admin/settlement-configuration/fund-wallet-config
        `,
        {
          currency,
          lowerBoundary: lboundary,
          upperBoundary: uboundary,
          rate,
          cap,
          base
        },
        {
          headers: {
            Authorization: `Bearer ${sessionStorage.getItem('token')}`
          }
        }
        )
      .then(res => {
        if (res.status === 201) {
          getCOnfigsSettlemts()
          setLoad(false)
          setMsg('Settlement Created Successfully')
          setSuccess(true)
          setError(false)
          getCOnfigsSettlemts()

          setTimeout(() => {
            setSuccess(false)
            setCreate(false)
          },2500)
        }
      })
      .catch(err => {
        setLoad(false)
        if (err.response !== undefined) {
          setMsg(err.response.data.message)
          setError(true)
          setSuccess(false)
          setTimeout(() => {
            setError(false)
          },2500)
        } else {
          setMsg('Connection Error')
          setError(true)
          setSuccess(false)
          setTimeout(() => {
            setError(false)
          },2500)
        }
      })
    } else {
      setFill(true)
      setTimeout(() => {
        setFill(false)
      }, 2500)
    }
  }

  const update = () => {
    setLoad(true)
    axios
    .put(
      `${config.baseUrl}/api/v1/admin/settlement-configuration/fund-wallet-config/${id}`,
      {
        currency,
        lowerBoundary: lboundary,
        upperBoundary: uboundary,
        rate,
        cap,
        base,
      },
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      if (res.status === 200) {
        setLoad(false)
        getCOnfigsSettlemts()
        setMsg('Settlement Updated Successfully')
        setSuccess(true)
        setError(false)
        getCOnfigsSettlemts()
        setTimeout(() => {
          setSuccess(false)
          setCreate(false)
        },2500)
      }
    })
    .catch(err => {
      setLoad(false)
      if (err.response !== undefined) {
        setMsg(err.response.data.message)
        setError(true)
        setSuccess(false)
        setTimeout(() => {
          setError(false)
        },2500)
      } else {
        setMsg('Connection Error')
        setError(true)
        setSuccess(false)
        setTimeout(() => {
          setError(false)
        },2500)
      }
    })
  }

  useEffect(() => {
    getCOnfigsSettlemts()
  }, [])

  const getCOnfigsSettlemts = () => {
    axios
    .get(
      `${config.baseUrl}/api/v1/admin/settlement-configuration/fund-wallet-configs
      `,
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      if (res.data) {

        setSettlementsDatas(res.data.map((data) => ({
          id:data.id,
          lowerBoundry:
          data.currency === 'NGN'
          ? 'N' + Intl.NumberFormat('en-US').format(data.lowerBoundary)
          : '$' + Intl.NumberFormat('en-US').format(data.lowerBoundary),
          upperBoundry:
          data.currency === 'NGN'
          ? 'N' + Intl.NumberFormat('en-US').format(data.upperBoundary)
          : '$' + Intl.NumberFormat('en-US').format(data.upperBoundary),
          rate: data.rate,
          cap: data.cap,
          base:data.base,
          lower: data.currency === 'NGN'
          ?  data.lowerBoundary
          : data.lowerBoundary,
          currency: data.currency,
          upper:    data.currency === 'NGN'
          ? data.upperBoundary
          : data.upperBoundary,

        })))

      }
    })
    .catch(err => {
      if (err) {
      }
    })
  }

  const changeStatusConfirm = (id, e) => {
    e.preventDefault()
    Modal.confirm({
      title: `Are you sure you want to delete this settlement?`,
      icon: <ExclamationCircleOutlined />,
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      onOk () {
        deleteFunc(id)
      },
      onCancel () {
        console.log('Cancel')
      }
    })
  }

  const deleteFunc = id => {
    axios
    .delete(
      `${config.baseUrl}/api/v1/admin/settlement-configuration/fund-wallet-config/${id.id}
      `,
      {
        headers: {
          Authorization: `Bearer ${sessionStorage.getItem('token')}`
        }
      }
      )
    .then(res => {
      if (res.status === 200) {
        getCOnfigsSettlemts()
        setMsg('Settlement Deleted Successfully')
        setDelErr(true)
        setDelSuccess(false)
      }
    })
    .catch(err => {
      if (err.response !== undefined) {
        setMsg(err.response.data.message)
        setDelErr(true)
        setTimeout(() => {
          setDelErr(false)
        },2500)
      } else {
        setMsg('Connection Error')
        setDelErr(true)
        setTimeout(() => {
          setDelErr(false)
        },2500)          
        setDelSuccess(false)
      }
    })
  }

  const createSettlementFunc = () => {
    setUpdateState(false)
    setCreate(true)
  }

  const updateFunc = id => {
    console.log(id)
    setID(id.id)
    setCurrency(id.currency)
    setLBoundary(id.lower)
    setUBoundary(id.upper)
    setCap(id.cap)
    setRate(id.rate)
    setBase(id.base)
    setUpdateState(true)
    setCreate(true)

  }

  console.log(lboundary)

  return (
    <CRow>
    <CCol>
    {deleteErr && <CAlert color='danger'>{msg}</CAlert>}
    <CCard>
    <CCardHeader>
    <div className='d-flex justify-content-between align-items-center'>
    <div>Exchange settlement</div>
    <button
    type='button'
    class='btn btn-primary mr-2'
    onClick={createSettlementFunc}
    >
    Create Settlement
    </button>
    </div>
    </CCardHeader>
    <CCardBody>
    <CDataTable
    items={setlementsDatas}
    fields={[
      { key: 'lowerBoundry', name: 'Lower Boundry' },
      { key: 'upperBoundry', name: 'Upper Boundry' },
      { key: 'rate', name: 'Rate ' },
      { key: 'cap', name: 'Cap' },
      { key: 'base', name: 'Base' },
      { key: 'currency', name: 'Currency' },
      { key: 'lower', name: 'Exact Lower' },
      { key: 'upper', name: 'Exact Upper'},

      {
        key: 'Actions',
        name: 'Actions'
      }
      ]}
      hover
      striped
      activePage={page}
      scopedSlots={{
        Actions: item => (
          <td className='d-flex'>
          <button
          type='button'
          class='btn btn-success mr-2'
          onClick={updateFunc.bind(this, item)}
          >
          Update
          </button>
          <button
          type='button'
          class='btn btn-danger'
          onClick={changeStatusConfirm.bind(this, item)}
          >
          Delete
          </button>
          </td>
          )
      }}
      clickableRows
      />

      </CCardBody>
      </CCard>
      </CCol>

      <Modal
      title={!updateState ? 'Create Settlement' : 'Update  Settlement'}
      visible={createSettlement}
      footer={null}
      maskClosable={false}
      onCancel={closeCreate}
      >
      <div className='container'>
      {success && <CAlert color='success'>{msg}</CAlert>}
      {error && <CAlert color='danger'>{msg}</CAlert>}
      {fill && <p className="text-danger text-center">Fields are all required</p>}


      <form>
      {!updateState ? (
        <>
        <CFormGroup>
        <CLabel htmlFor='name'>Currency</CLabel>
        <CSelect
        custom
        name='ccmonth'
        id='ccmonth'
        onChange={e => setCurrency(e.target.value)}
        value={currency}
        >
        <option selected>Select</option>

        <option value='USD'>USD</option>
        <option value='NGN'>NGN</option>
        </CSelect>{' '}
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Lower Boundary</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setLBoundary(e.target.value)}
        value={lboundary}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Upper Boundary</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setUBoundary(e.target.value)}
        value={uboundary}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Rate</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setRate(e.target.value)}
        value={rate}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Cap</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setCap(e.target.value)}
        value={cap}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Base</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setBase(e.target.value)}
        value={base}
        />
        </CFormGroup>
        </>
        ) : (
        <>
        <CFormGroup>
        <CLabel htmlFor='name'>Currency</CLabel>
        <CSelect
        custom
        name='ccmonth'
        id='ccmonth'
        onChange={e => setCurrency(e.target.value)}
        value={currency}
        >
        <option value='USD'>USD</option>
        <option value='NGN'>NGN</option>
        </CSelect>{' '}
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Lower Boundary</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setLBoundary(e.target.value)}
        value={lboundary}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Upper Boundary </CLabel>
        <CInput id='name' type='number'                     onChange={e => setUBoundary(e.target.value)}
        required value={uboundary} />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Rate</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setRate(e.target.value)}
        value={rate}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Cap</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setCap(e.target.value)}
        value={cap}
        />
        </CFormGroup>
        <CFormGroup>
        <CLabel htmlFor='name'>Base</CLabel>
        <CInput
        id='name'
        type='number'
        required
        onChange={e => setBase(e.target.value)}
        value={base}
        />
        </CFormGroup>
        </>

        )}
        <br />
        <div className='d-flex justify-content-end'>
        <button
        type='button'
        class='btn btn-primary mr-2'
        onClick={!updateState ? addSettlement : update}
        >
        {load ? (
          <div
          class='spinner-border'
          role='status'
          style={{ width: '1rem', height: '1rem' }}
          >
          <span class='sr-only'>Loading...</span>
          </div>
          ) : (
          'Submit'
          )}
          </button>
          </div>
          </form>
          </div>
          </Modal>
          </CRow>
          )
}

export default Users
